import { Button, Divider, Input, Modal, Radio, Table } from "antd";
import AvatarStatus from "components/shared-components/AvatarStatus";
import { SearchOutlined } from "@ant-design/icons";
import Flex from "components/shared-components/Flex";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";

function CustomerModal(props) {
  const { showModal, handleOk, handleCancel, visible, customerList, getCustomerList } = props;
  const [customer, setCustomer] = useState();
  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      setCustomer(selectedRows[0]);
    },
    getCheckboxProps: (record) => ({
      disabled: record.name === "Disabled User",
      name: record.name,
    }),
  };
  const columns = [
    {
      title: "",
      dataIndex: "name",

      render: (_, record) => (
        <Flex>
          <AvatarStatus size={30} name={record.name} subTitle={record.company} />
        </Flex>
      ),
    },
    {
      title: "",
      dataIndex: "phoneNumber",
    },
    {
      title: "2",
      dataIndex: "secondPhoneNumber",
    },
    {
      title: "",
      dataIndex: "address",
    },
  ];
  const onSearch = (e) => {
    const value = e.currentTarget.value;
    getCustomerList(value);
  };
  const initializeList = (e) => {
    if (e.currentTarget.value === "") {
      getCustomerList();
    }
  };
  return (
    <div>
      <Modal
        title="Basic Modal"
        visible={visible}
        onOk={() => {
          handleOk(customer);
        }}
        onCancel={handleCancel}
        width={"40%"}
      >
        <Input
          placeholder="Search"
          prefix={<SearchOutlined />}
          onPressEnter={(e) => onSearch(e)}
          onChange={(e) => initializeList(e)}
        />
        <Table
          columns={columns}
          rowSelection={{
            type: "radio",
            ...rowSelection,
          }}
          rowKey={"id"}
          dataSource={customerList ? customerList.result : ""}
        />
      </Modal>
    </div>
  );
}

export default CustomerModal;
